*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-text-color: #fcfcfc;
  --secondary-black-text-color: #131313;
  --secondary-grey-text-color: #ababab;
  --primary-title-color: #000;
  --card-green-color: #aec6a5;
  --card-brown-color: #d6936d;
  --card-yellow-color: #efd478;
  --card-background-color: #fff;
  --section-color: #84a178;
  --lines-color: #e1e1e1;
  --primary-btn-color: #f8d24e;
  --secondary-btn-color: #0a8817;
  --time-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  background-color: var(--section-color);
  color: var(--primary-text-color);
  letter-spacing: .04em;
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

body.modal-open {
  overflow: hidden;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.button {
  color: var(--primary-title-color);
  cursor: pointer;
  border: none;
  border-radius: 10px;
  align-items: center;
  padding: 12px 35px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
  display: inline-flex;
}

.button__svg {
  width: 7px;
  height: 11px;
  fill: none;
  stroke: currentColor;
  margin-left: 16px;
}

.button:focus, .button:active {
  color: var(--primary-text-color);
}

.button_fill {
  background-color: var(--primary-btn-color);
  transition: background-color 250ms var(--time-function), color 250ms var(--time-function);
}

.button_fill:hover, .button_fill:focus, .button_fill:active {
  background-color: var(--secondary-btn-color);
}

.button_transparent {
  border: 2px solid var(--primary-btn-color);
  transition: border-color 250ms var(--time-function), color 250ms var(--time-function);
  background-color: rgba(0, 0, 0, 0);
  padding: 10px 33px;
}

.button_transparent:hover, .button_transparent:focus, .button_transparent:active {
  border-color: var(--secondary-btn-color);
}

.button_color-light {
  color: var(--primary-text-color);
}

@media screen and (max-width: 1199px) {
  .button_small {
    border-radius: 8px;
    padding: 6px 13px;
    font-size: 8px;
    font-weight: 600;
    line-height: 1.25;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.title {
  text-transform: uppercase;
}

.title_light {
  color: #fcfcfc;
}

.title_dark {
  color: #000;
}

.title_header {
  font-family: Seymour One;
  font-size: 30px;
  font-weight: 400;
  line-height: 1.27;
}

.title_label {
  font-size: 30px;
  font-weight: 800;
  line-height: 1.23;
}

.subtitle {
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
}

.subtitle_light {
  color: #fcfcfc;
}

.subtitle_dark {
  color: #000;
}

.modal-title {
  color: var(--primary-title-color);
  font-family: Seymour One;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

@media screen and (min-width: 768px) {
  .modal-title {
    font-size: 20px;
    line-height: 1.4;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 80px;
}

.logo-icon {
  fill: #f8d24e;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 90000;
  transition: opacity 250ms var(--time-function), visibility 250ms var(--time-function);
  background-color: rgba(0, 0, 0, .3);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is_hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  width: 88%;
}

@media screen and (max-width: 767px) {
  .modal {
    max-width: 450px;
    height: 966px;
  }
}

.modal {
  background-color: var(--card-background-color);
  border: 2px solid var(--primary-btn-color);
  border-radius: 10px;
  padding: 28px;
  position: absolute;
  top: 12px;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (min-width: 768px) {
  .modal {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.modal__close-button {
  width: 33px;
  height: 33px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0 solid rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.modal__btn-svg {
  transition: fill 250ms var(--time-function);
  position: absolute;
}

.modal__close-button:hover .modal__btn-svg, .modal__close-button:focus .modal__btn-svg {
  fill: var(--secondary-btn-color);
}

@media screen and (min-width: 768px) {
  .modal {
    height: 644px;
    width: 568px;
  }

  .modal__close-button {
    width: 59px;
    height: 59px;
  }
}

.modal__close-button:hover, .modal__close-button:focus {
  border-color: var(--secondary-btn-color);
}

@media screen and (max-width: 1199px) {
  .header-nav__list {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .header__btn {
    display: none;
  }
}

.modile-container {
  padding-top: 12px;
  padding-left: 20px;
  padding-right: 20px;
}

.mobile-box {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.mobile-menu__open-btn {
  height: 32px;
  width: 32px;
  fill: var(--primary-text-color);
  transition: fill 250ms var(--time-function);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding-top: 6px;
  padding-bottom: 6px;
}

.mobile-menu__open-btn:hover, .mobile-menu__open-btn:focus {
  fill: var(--primary-title-color);
}

.header-nav__logo-img {
  width: 37px;
  height: 40px;
}

.mobile-menu {
  width: 100%;
  height: 100%;
  z-index: 9000;
  background-color: var(--section-color);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 250ms var(--time-function), visibility 250ms var(--time-function), transform 500ms var(--time-function);
  padding-bottom: 63px;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  overflow-y: scroll;
  transform: translateY(0);
}

.mobile-menu__close-btn {
  height: 32px;
  width: 32px;
  fill: var(--primary-text-color);
  transition: fill 250ms var(--time-function);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

.mobile-menu__close-btn:hover, .mobile-menu__close-btn:focus {
  fill: var(--primary-title-color);
}

.mobile-menu__list {
  text-align: center;
  margin-top: 50px;
}

.mobile-menu__link {
  transition: color 250ms var(--time-function);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.21875;
}

.mobile-menu__link:hover, .mobile-menu__link:focus {
  color: var(--primary-title-color);
}

.mobile-menu__item {
  margin-bottom: 32px;
}

.mobile-menu__item:last-of-type {
  margin-bottom: 0;
}

.mobile-menu__btn {
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 33px;
  padding-right: 33px;
  display: block;
}

.mobile-menu__svg {
  margin-left: 10px;
}

